@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
*{box-sizing:border-box}body{width:100vw;height:100vh;margin:0;padding:0;background-color:#333;display:flex;justify-content:center;align-items:center}

.avatar{display:flex;flex-direction:column;align-items:center;color:#f5f5f5}.avatar__image,.avatar__popover-pick{position:relative;display:flex;justify-content:center;align-items:center;width:60px;height:60px;border-radius:50%;margin-bottom:16px;background-color:#bdbdbd;color:#2c3033}.avatar__image:hover>.border,.avatar__image:focus>.border,.avatar__popover-pick:hover>.border,.avatar__popover-pick:focus>.border{outline:0;border:1px solid #9ba0a3}.avatar__image>img,.avatar__popover-pick>img{width:100%;height:100%;border-radius:50%}.avatar__popover{position:relative;width:calc(4 * 60px + 5 * 14px);border-radius:2px;padding:14px 7px;background-color:#2c3033;box-shadow:2px 2px 10px #666;-webkit-transform:scale(0);transform:scale(0)}.avatar__popover h5{width:100%;margin:0 0 14px 0;text-align:center;font-family:'Source Sans Pro', sans-serif;font-weight:normal;font-size:16px;color:#f9f9f9}.avatar__popover-list{display:flex;flex-wrap:wrap;margin:0;padding:0;list-style-type:none}.avatar__popover-pick{position:relative;margin:7px;outline:0}.avatar__popover-pick:hover>.border,.avatar__popover-pick:focus>.border{border:3px solid #9ba0a3;background-color:rgba(0,0,0,0.2)}.avatar__popover-triangle{position:absolute;top:-8px;left:calc(50% - 8px);width:0;height:0;border-style:solid;border-width:0 8px 8px 8px;border-color:transparent transparent #2c3033 transparent}.avatar .border{position:absolute;top:-1px;bottom:-1px;left:-1px;right:-1px;border-radius:50%}.avatar .border--big{top:-3px;bottom:-3px;left:-3px;right:-3px}@-webkit-keyframes popover{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes popover{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes fetching{0%{border-width:3px;border-style:solid;border-color:transparent #7aa1b2 #7aa1b2 #7aa1b2}100%{border-width:3px;border-style:solid;border-color:transparent #7aa1b2 #7aa1b2 #7aa1b2;-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes fetching{0%{border-width:3px;border-style:solid;border-color:transparent #7aa1b2 #7aa1b2 #7aa1b2}100%{border-width:3px;border-style:solid;border-color:transparent #7aa1b2 #7aa1b2 #7aa1b2;-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.app{display:flex;justify-content:center;align-items:flex-start;width:500px;height:400px;padding:10px;border:1px solid #526d7a;box-shadow:0 20px 40px -10px #262626;border-radius:15px;background-color:#ccc}

